body {
  margin: 0;
  font-family: Roboto, sans-serif;
  background-color: #D3D3D3;
}

header {
  background-color: #FFF;
  padding: 10px 0;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  text-decoration: none;
  position: relative;
  margin-left: 50px;
  display: flex;
  align-items: center;
}

.logo img {
  width: 40px;
  height: 40px;
}

nav {
  width: 1200px;
  margin-left: 509px;
}

nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

nav li {
  margin: 0 15px;
}

nav a {
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  height: 60px;
  line-height: 60px;
  display: block;
  color: #000;
}


nav a:hover {
  border-radius: 4px;
  border-bottom: 4px solid #00ACDC;
  position: relative;
}

.Color {
  color:#00ACDC ;
}
